import React from 'react';
import { Button } from 'react-bootstrap';

interface AppItemProps {
  item: {
    id: string;
    name: string;
    url: string;
  };
  index: number;
  deleteModeActive: boolean;
  handleDeleteWebsite: (id: string) => void;
  onLongPress: () => void;
  handleDragStart: (e: React.DragEvent<HTMLDivElement>, index: number) => void;
  handleDragOver: (e: React.DragEvent<HTMLDivElement>) => void;
  handleDrop: (e: React.DragEvent<HTMLDivElement>, dropIndex: number) => void;
  onTouchStart: (e: React.TouchEvent<HTMLDivElement>, index: number) => void;
  onTouchMove: (e: React.TouchEvent<HTMLDivElement>) => void;
  onTouchEnd: (e: React.TouchEvent<HTMLDivElement>) => void;
  handleShowEdit: (item: any, index: number) => void;
  getFaviconUrl: (url: string) => { primary: string; fallback: string };
}

const AppItemComponent: React.FC<AppItemProps> = ({ item, index, deleteModeActive, handleDeleteWebsite, onLongPress, handleDragStart, handleDragOver, handleDrop, onTouchStart, onTouchMove, onTouchEnd, handleShowEdit, getFaviconUrl }) => {
  const handleClick = (e: React.MouseEvent<HTMLElement> | React.TouchEvent<HTMLElement>) => {
    const deleteButton = e.currentTarget.querySelector('.delete-btn');
      if (e.target === deleteButton || (deleteButton && deleteButton.contains(e.target as Node))) {
        return;
      }

    if (deleteModeActive) {
      handleShowEdit(item, index);
    } else {
      window.open(item.url, '_blank');
    }
  };

  const handleDelete = (e: React.MouseEvent<HTMLButtonElement> | React.TouchEvent<HTMLButtonElement>) => {
    e.preventDefault();
    e.stopPropagation();
    handleDeleteWebsite(item.id);
  };

  return (
    <div
      className="col-md-2 mb-3 app-block-wrapper"
      onClick={handleClick}
      onContextMenu={(e) => {
        e.preventDefault();
        onLongPress();
      }}
      draggable={deleteModeActive}
      onDragStart={(e) => handleDragStart(e, index)}
      onDragOver={handleDragOver}
      onDrop={(e) => handleDrop(e, index)}
      onTouchStart={(e) => onTouchStart(e, index)}
      onTouchMove={onTouchMove}
      onTouchEnd={onTouchEnd}
    >
      <div className={`card ${deleteModeActive ? 'delete-mode' : ''}`}>
        {deleteModeActive && (
          <Button variant="danger" className="delete-btn" onClick={handleDelete}>
            &times;
          </Button>
        )}
        <div className="card-body text-center">
          <img
            src={getFaviconUrl(item.url).primary}
            alt="Favicon"
            className="favicon mb-2"
            onError={(e) => (e.currentTarget.src = getFaviconUrl(item.url).fallback)}
            style={{ width: '84px', height: '84px' }}
            draggable="false"
          />
          <h5 className="card-title">{item.name}</h5>
        </div>
      </div>
    </div>
  );
};

export default AppItemComponent;